@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
* {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif; }

body {
  background: #393e46;
  margin: 0;
  padding: 0; }

.button {
  background: #d65a31;
  border: none;
  color: #eeeeee;
  font-size: 1rem;
  padding: 1rem 1.5rem;
  outline: none;
  -webkit-transition: all .1s;
  transition: all .1s; }

.button-round {
  border-radius: 500px;
  bottom: 2rem;
  font-size: 2rem;
  left: 2rem;
  position: absolute;
  height: 4.5rem;
  width: 4.5rem; }

.button-round.active {
  background: #c54920; }

.button-round__dropdown {
  background: white;
  border-radius: 8px;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.2);
  bottom: 8rem;
  left: 2rem;
  padding: 0;
  position: absolute;
  text-align: center;
  z-index: 3; }
  .button-round__dropdown h4 {
    margin: 1rem 0; }
  .button-round__dropdown ul {
    padding: 0;
    margin: 1rem 0; }
    .button-round__dropdown ul li {
      list-style: none;
      padding: 1rem 4rem; }
      .button-round__dropdown ul li:hover {
        background: #eeeeee;
        cursor: pointer; }
  .button-round__dropdown::after {
    border-left: 17px solid transparent;
    border-right: 17px solid transparent;
    border-top: 20px solid #d65a31;
    bottom: -1rem;
    content: "";
    height: 0;
    left: 1.2rem;
    position: absolute;
    width: 0;
    z-index: -1; }

.graphs {
  align-items: center;
  background: #191A1A;
  color: white;
  display: flex;
  flex-direction: column;
  padding: 2rem 1rem;
  width: 30vw; }
  .graphs h3 {
    font-size: 1.5rem;
    margin-bottom: 0.25rem; }

.rv-xy-plot__series--label-text {
  color: white !important; }

.mapboxgl-ctrl-bottom-left, .mapboxgl-ctrl-bottom-right {
  display: none; }

.dropdown-form__header {
  background: #eeeeee;
  border-radius: 6px 6px 0 0; }
  .dropdown-form__header h4 {
    color: #393e46;
    font-size: 0.9rem;
    letter-spacing: 0.5px;
    margin: 0;
    padding: 1rem;
    text-transform: uppercase; }

.dropdown-form__body {
  border-radius: 0 0 6px 6px;
  padding: 1.5rem 1rem 0 1rem; }

.dropdown-form__body form select {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none; }

.dropdown-form__body form select, .dropdown-form__body form input {
  border: 1px solid #eeeeee;
  display: block;
  font-size: 1rem;
  margin-bottom: 0.75rem;
  outline: none;
  padding: 0.75rem;
  width: 100%; }

.dropdown-form__body form button {
  border-radius: 0 0 6px 6px;
  cursor: pointer;
  display: block;
  margin-top: 1.5rem;
  margin-left: -1rem;
  width: 116.8%; }

